import { Timestamps } from "@rp/react/rpjson/types"

export const convertRpTimestampsToUTC = (timestamps: Timestamps): Timestamps => {
  const ret = Object.entries(timestamps).map(([key, value]) => [
    key,
    value.endsWith("Z") ? value.replace(" ", "T") : `${value.replace(" ", "T")}Z`,
  ])
  return Object.fromEntries(ret)
}
